            align-items: center;
    height: 80vh;
    width: 100%;
    color: white;
}

.scroll-div{
    scroll-snap-align: center;
    opacity: 0;
    transition: opacity 2s;
}
.scroll-div.in-view {
    opacity: 1;
}

#title {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 80vw;
    height: 80vh;
    color: white;
}

.info-div{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 300px;
    width: 60vw;
    margin: auto;
}

.info-img{
    background-color: gray;
    height: 275px;
    border: 2px solid black;
}

.info-text{
    padding: 30px;
}

.info-text > span {
    font-size: 1.7rem;
}


#main-title {
    font-family: 'Dorsa', sans-serif;
    font-size: 30vh;
}

#sub-title{
    font-size: 20px;
}

.pointer {
    position: fixed;
    bottom: 50px;
    font-size: 1.3rem;
    -webkit-animation: bounce 2s;
            animation: bounce 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    margin: auto;
    color: white;
    left: 50%;
}

.invisible {
    display: none;
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {bottom: 50px;} 
    40% {bottom: 60px;} 
    60% {bottom: 60px;} 
 }

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {bottom: 50px;} 
    40% {bottom: 60px;} 
    60% {bottom: 60px;} 
 } 

 @media (max-width: 700px) {
    #main-title{
        font-size: 20vh;
        text-align: center;
     }

     .info-text{
        padding: 0;
        width: 100%;
    }

     .info-img{
        display: none;
    }

    .info-div{
        display: block;
        height: 300px;
        margin: auto;
    }

    .scroll-div{
        opacity: 1;
    }

 }
